*{
    margin: 0;
    padding: 0;
}
body {
    background-color: rgb(108, 108, 108);
}


.header_image {
    margin-top: 0%;
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
}

header {
    width: 100%;
    height: 150px;
    position: relative;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
}
#Container{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 10px 10px;
    width: 100%;
}

.card {
    font-family: DejaVu Sans Mono, monospace;
    border: 2px solid rgb(78, 78, 78);
    border-radius: 5px;
    background-image: linear-gradient(rgb(88, 88, 88), rgb(255, 255, 255));
    text-align: center;

    background-color: aqua;
}


